@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;400;600&display=swap);
*,
*:before,
*:after{
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

html,body,#root{
  width: 100%;
  height: 100%;
  background: #192A56;
  font-family: 'Poppins', sans-serif;
}
a{
  text-decoration: none;
  color: inherit;
}
li{
  list-style-type: none;
}

.main-container{
  display: flex;
  flex-direction: column;
  width: 100%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}
.section{
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  flex-direction: column; 
  height: 100vh;
  margin: 20px auto; 
  width: 95%;
}


.intro{
    font-size: 3rem;
    margin-left: 25px;
}

.down-title{
    font-weight: 200;
    font-size: 1.4rem;
    width: 70%;
    margin:auto;
    text-align:center;
}

/* .more{
    margin: 30px 0px;
}
 */

/* ------------- */
.main-heading{
    width: 100%;
    max-width: 1000px;
    height: 400px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: relative;
    bottom: 100px;
}
.name-container{
    height: 10%;
}
.first-name{
    font-weight: 200;
    font-size: 2.5rem;
    color: #DDD9D9;
    padding-left: 15px;
}
.last-name{
    position: relative;
    bottom: 15px;
    font-size: 3rem;
    color: #DDD9D9;
}
.welcome{
    height: 270px;
    font-size: 1.3rem;
    width: 100%;
    color: #E0e0e0;
    position: relative;
    bottom: 30px;
    /* left: 15px; */
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    
}
.top{
    position: absolute;
    right: 3px;
    /* top: 10px; */
    top: -30px;
    width: 232px;
    height: 184px;
    background: rgba(196, 196, 196, 0.08);
    mix-blend-mode: normal;
    border-radius: 23px;
}
.bottom{
    padding: 15px;
    width: 95%;
    height: 376px;
    background: rgba(9, 23, 44, 0.26);
    mix-blend-mode: normal;
    border-radius: 21px;
}
.cta-section{
    width: 332px;
    height: 120px;
    display: flex;
    position: relative;
    bottom: 90px;
    margin-top: 50px;
    right: 20px;
    flex-direction: column;
    justify-content: space-between;
}
.cta-before{
    position: relative;
    top: 40px;
    left: 20px;
    z-index: 1;
    width: 267px;
    height: 70%;
    background: #09172C;
    border-radius: 10px;
}
.cta-section>a{
    font-size: 3.5rem;
    font-weight: 700;
    color: #e0e0e0;
    align-self: flex-end;
    height: 50%;
    z-index: 2
}

@media screen and (min-width: 768px){
    .main-heading{
        top: 70px;
    }
    .top{
        top: 0%;
        height: 250px;
        width: 500px;
    }
    .bottom{
        height: 500px
    }
    .first-name{
        font-size: 3rem;
    }
    .last-name{
        font-size: 5rem;
    }
    .welcome{
        font-size: 2rem;
        width: 100%;
        top: 100px;
    }
    .cta-section{
        width: 450px;
        top: 90px;
        left: 5px;
        margin: auto;
        align-items: center;
    }
    .cta-section>.cta-before{
        height:100px;
        display: flex;
        width: 100%;
        position: unset;
    }
    .cta-section>a{
        align-self: unset;
        height: 20px;
        position: relative;
        bottom: 50px;
        font-size: 4rem;
    }
}

@media screen and (min-width: 900px){
    .main-heading{
        top: 80px;
    }
    .cta-section{
        top: 80px
    }
}
.navigation{
    display: flex;
    width: 100%;
    height: 50px;
    position: relative;
    bottom: 0%;
    justify-content: space-between;
    
}
.menu-list{
    background: #09172c;
    position: fixed;
    transform: translateY(100%);
    opacity: 0;
    color: #E0E0E0;
    top:0px;
    left: 0px;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column; 
    align-items: center; 
    transition: all 0.4s ease-in;
}
.show{
    transform: translateY(0%);
    z-index: 1000;
    opacity: 1;
}
.social-media-section,.link-section{
    width: 80%;
    height: 40%;
    display: flex;
    flex-direction: column;
    justify-content: space-around; 
}
.social-media-section{
    align-items: flex-end;
}
.social-icon{
    width: 60px;
    height: 60px;
    background: #E0E0E0;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #111;
    font-size: 2rem;
}
.menu-button-section{
    position: fixed;
    width:100%;
    background: #122149;
    padding: 5px;
    bottom: 0%;
    right: 0%;
    display: flex;
    justify-content: center;
    align-items: center;
    border-top-left-radius: 40px ;
    border-top-right-radius: 40px;
    z-index: 1100;
}
.menu-button{
    border:none;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    background: #09172c;
    cursor: pointer;
    transition: all 0.5s ease;
}
.rotate{
    transform: rotate(90deg);
}
.fa-ellipsis-v{
    color: #E0E0E0; 
}

.link-section>li{
    border-bottom: 1px solid #192A56;
    padding-bottom: 15px;
}
.link-section>li>a{
    font-size: 2rem;
}

@media screen and (min-width: 900px){
    .navigation{
       position: unset;
       z-index: 1000;
    }
    .menu-button-section{
        display: none;
    }
    .menu-list{
        background: #09172c;
        position: fixed;
        transform: unset;
        opacity: 1;
        color: #E0E0E0;
        top:0px;
        left: 0px;
        padding: 10px;
        width: 100%;
        height: 100px;
        display: flex;
        flex-direction: row; 
        align-items: center; 
        justify-content: space-between;
        transition: all 0.4s ease-in;
    }
    .social-media-section,.link-section{
        padding: 20px;
        display: flex;
        flex-direction: row;
        justify-content: c;
        align-items: center;
    }
    .link-section{
        width: 800px;
    }

    .link-section>li{
        border-bottom: none;
        padding-bottom: 0px;
    }
    .social-media-section{
        width: 400px;
        height: 100%;

    }

}

.project-card{
    position: relative;
    margin: 60px 0px;
    padding: 0px 30px;
}

.img-container{
    position: relative;
    width:250px;
    max-width: 450px;
    height: 300px;
    overflow: hidden;
    border-radius: 13px;
}
.img-container:hover .img-container-cover, .img-container:active .img-container-cover{
    transform: scale(1);
}
.img-container-cover{
    width: 100%;
    height: 100%;
    background: #09172cf8;
    position: absolute;
    top:0px;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transform: scale(0);
    transition: all 0.4s ease;
    border-radius: 13px;
}
.card-link{
    background: #e0e0e0;
    font-weight: 700;
    padding: 5px 20px;
    border-radius: 10px;
    margin-top: 10px;
}
img{
    width: 100%;
    height: 100%;
}
.img-container-cover p{
    font-size: 2rem;
    color: #fff;
    text-align: center;
}
.project-card:nth-child(odd){
    align-self: flex-start;
}
.project-card:nth-child(odd)>.project-name{
   right: -20px;
}
.project-card:nth-child(odd)>.rectangle{
   right: -10px;
   top: -30px;
}
.project-card:nth-child(even){
    align-self: flex-end;
}
.project-card:nth-child(even)>.project-name{
    left: -20px;
}
.project-card:nth-child(even)>.rectangle{
    left: -10px;
    top: -30px;
 }
.project-name{
    position: absolute;
    top: -20px;
    font-size: 1.5rem;
    color: #e0e0e0;
    font-weight: 500;
    z-index: 100;
    display: flex;
    flex-direction: column;
}
.text{
    color: #fff;
    font-size: 1.3rem;
    text-align: center;
    width: 70%;
    margin: auto;
    position: relative;
}
.tag{
    font-size: 1.3rem;
    color: lightslategrey;
    font-weight: 200;
    font-family: cursive;
}
.margin-top{
    margin-top: 500px;
    padding-top: 20px;
}
.rectangle{
    position: absolute;
    width: 211.54px;
    height: 239px;
    background: rgba(9, 23, 44, 0.6);
    border-radius: 10px;
}
.projects{
    margin-top: 150px;
    
}

@media screen and (min-width: 768px){
    .projects{
        margin-top: 150px;
    }
}


@media screen and (min-width: 900px){
    .projects{
        flex-flow: row wrap;
        margin-top: unset;
    }
    .project-card{
        position: relative;
        margin: unset;
        padding: 0;
        margin: 30px;
    }

    .img-container{
        
        position: relative;
        width:450px;
        height: 300px;
        border-radius: 13px;
    }
    .img-container>img{
        width: 100%;
        height: 100%;
    }
    .project-card:nth-child(odd){
        align-self: unset;
    }
    .project-card:nth-child(even){
        align-self: unset;
    }
    .project-card>.rectangle{
        width: 105%;
        height: 90%;
    }

    .project-name{
        flex-direction: row;
        position: static;
        position: initial;
        padding-bottom: 20px;
        left: unset;
        right: unset;
        justify-content: center;
        z-index: 1000;
        color: #fff;
    }
}
